function destroyViewer () {
  if (VIEWER) {
    VIEWER.destroy()
    setViewer(null)
  }
  if (MsgRecordViewer) {
    MsgRecordViewer.destroy()
    setMsgRecordViewer(null)
  }
}
function initViewer () {
  if (!VIEWER) {
    const viewer = new Viewer(document.querySelector('#chat-list'), {
      viewed () {
        this.viewer.zoomTo(0.8)
      },
      url (image) {
        return image.src.replace('imageView&thumbnail=180x0&quality=85', '')
      },
      navbar: 0,
      toolbar: {
        zoomIn: 4,
        zoomOut: 4,
        oneToOne: 0,
        reset: 4,
        prev: 0,
        play: {
          show: 0,
          size: 'large'
        },
        next: 0,
        rotateLeft: 4,
        rotateRight: 4,
        flipHorizontal: 4,
        flipVertical: 4
      }
    })
    setViewer(viewer)
  }
  VIEWER.update()
}

function initMsgRecordViewer () {
  if (!MsgRecordViewer) {
    const viewer = new Viewer(document.querySelector('#record-content'), {
      viewed () {
        this.viewer.zoomTo(0.8)
      },
      url (image) {
        return image.src.replace('imageView&thumbnail=180x0&quality=85', '')
      },
      navbar: 0,
      toolbar: {
        zoomIn: 4,
        zoomOut: 4,
        oneToOne: 0,
        reset: 4,
        prev: 0,
        play: {
          show: 0,
          size: 'large'
        },
        next: 0,
        rotateLeft: 4,
        rotateRight: 4,
        flipHorizontal: 4,
        flipVertical: 4
      }
    })
    setMsgRecordViewer(viewer)
  }
  MsgRecordViewer.update()
}

