function initNimWebRTC ({ state, commit, dispatch, rootState }) {
  const webCrt = WebRTC.getInstance({
    nim: NIM,
    container: document.getElementById('local-video-wrapper'),
    remoteContainer: document.getElementById('remote-video-wrapper'),
    debug: false // 
  })
  setNimWebRTC(webCrt)
  const checkDevice = Cookies.get('CHECK-DEVICE')
  if (checkDevice && checkDevice === 'checked') {
    console.log('')
  } else {
    checkDevices()
    Cookies.set('CHECK-DEVICE', 'checked', { expires: 1 })
  }
  webrtc.initWebRTCEvent()
}

async function NimCall ({ state, commit, dispatch, rootState }, account) {
  try {
    if (state.calling || state.callOther || state.beCalling) {
      vue.$Message.error('')
      return
    }
    commit('crtCallUser', state.currImTitleInfo)
    account = account || state.crtCallUser.nimAccid
    if (account) {
      let belongIdentityType = state.userInfos[account].consultLabel
      belongIdentityType = belongIdentityType === 0 ? 0 : belongIdentityType === 2 ? 1 : belongIdentityType === 5 ? 2 : '' // App 012
      NimWebRTC.call({
        type: WebRTC.NETCALL_TYPE_AUDIO,
        account,
        pushConfig: {
          enable: true,
          needBadge: true,
          needPushNick: true,
          pushContent: '~',
          custom: JSON.stringify({ // pushContentpushPayloadcustompush
            pushTitle: '',
            pushContent: '~',
            xrsUrl: 'IMSession',
            sessionId: state.userUID,
            advisorType: rootState.roleRelativeData.userInfo.crmRoleId,
            consultId: state.currImTitleInfo.id,
            belongIdentityType
          }),
          pushPayload: JSON.stringify({
            pushTitle: '',
            xrsUrl: 'IMSession',
            sessionId: state.userUID,
            advisorType: rootState.roleRelativeData.userInfo.crmRoleId,
            consultId: state.currImTitleInfo.id,
            belongIdentityType
          }),
          sound: ''
        },
        sessionConfig: {

        }
      }).then(obj => {
        commit('setCallOther', true)
        webrtc.netcallAccount = account
        webrtc.callAcceptedOrRejected = false
        webrtc.playRing('A', 1, () => {
          webrtc.playRing('E', 45)
        })
        console.log('call success', obj)
        // 
        webrtc.callTimer = setTimeout(function () {
          if (!webrtc.callAcceptedOrRejected) { // 
            console.log('')
            webrtc.clearRingPlay()
            webrtc.playRing('B', 1)
            webrtc.cancelCalling()
            // -
            webrtc.xesTrack.track('15-1520', {
              consult_id: state.crtCallUser.id,
              adviser: rootState.roleRelativeData.userInfo.crmRoleId,
              adviser_departmentID: rootState.roleRelativeData.userInfo.crmDeptId,
              adviser_departmentNAME: '',
              adviser_empNo: rootState.roleRelativeData.roleInfo.userWorkCode,
              adviser_citycode: rootState.roleRelativeData.userInfo.cityCode
            })
          }
        }, 1000 * 45)
      }, err => {
        // console.error(err)
        // console.log(JSON.stringify(err, null, 2))
        // 
        if (err && err.event && err.event.code === 11001) {
          vue.$Message.error({
            content: '',
            duration: 5
          })
          // -
          webrtc.xesTrack.track('15-1513', {
            consult_id: state.crtCallUser.id,
            adviser: rootState.roleRelativeData.userInfo.crmRoleId,
            adviser_departmentID: rootState.roleRelativeData.userInfo.crmDeptId,
            adviser_departmentNAME: '',
            adviser_empNo: rootState.roleRelativeData.roleInfo.userWorkCode,
            adviser_citycode: rootState.roleRelativeData.userInfo.cityCode
          })
          // dispatch('sendLocalMessage', '', account)
        } else {
          console.error(err)
          webrtc.cancelCalling()
        }
      })
    } else {
      vue.$Message.warning({
        content: '',
        duration: 5
      })
    }
  } catch (e) {
    console.error(e)
    if (e && e.desc) {
      vue.$Message.warning({
        content: '' + e.desc,
        duration: 5
      })
    } else {
      vue.$Message.warning({
        content: '',
        duration: 5
      })
    }
  }
}
