/* session.js */
function startSession ({ state, commit, dispatch, rootState }, account) {
  // sessionlist
  const sessionlist = state.sessionlist
  let currentSession = false

  sessionlist.forEach(item => {
    if (item.to === account) {
      currentSession = true
    }
  })
  if (!currentSession) {
    // 
    NIM.insertLocalSession({
      scene: 'p2p',
      to: account,
      done: async (error, obj) => {
        console.log('' + (!error ? '' : ''), error, obj)
        await onSessions([obj.session || { id: `p2p-${account}`, to: account }])
        dispatch('setCurrSession', `p2p-${obj.session.to}`)
        dispatch('setStudentContactDetail', state.userInfos[account])
        commit('setShowMini', false)
      }
    })
  } else {
    dispatch('setCurrSession', `p2p-${account}`)
    dispatch('setStudentContactDetail', state.userInfos[account])
    commit('setShowMini', false)
  }
}
async function gainUpdateSessionStuInfo (sessions) {
  // let netEaseIds = []
  if (!Array.isArray(sessions)) {
    return
  }
  try {
    const roleId = store.state.roleRelativeData.userInfo.crmRoleId
    const data = await getStudentContactByIdentityId({
      advisorType: roleId,
      nimAccIds: [sessions[0].to].join(',')
    })
    store.state.imchat.originSessionList = data
    let sessionsArr = Object.values(data)
    sessionsArr = sessionsArr.map(item => {
      if (item.studentId) {
        item.netEaseId = item.studentId
        item.nimAccid = item.studentId
      }
      return item
    })
    // 
    store.commit('imchat/updateUserInfo', sessionsArr)
    // debugger
    //  curreSession
    if (store.state.imchat.currSessionId && roleId) {
      // if (store.state.imchat.currSessionId && roleId === 8) { TODO 0424
      const currentStuId = store.state.imchat.currSessionId.replace('p2p-', '')
      const usersInfos = store.state.imchat.userInfos
      store.dispatch('imchat/setStudentContactDetail', usersInfos[currentStuId])
    }
    // 
    await store.dispatch('imchat/searchUsers', {
      accounts: sessions[0].to
    })
  } catch (err) {
    // debugger
    Vue.prototype.$Message.error({
      content: '' + err.message
    })
  }
}
let getDetail = false
async function setStudentContactDetail ({ commit }, session) {
  if (getDetail) {
    return
  }
  getDetail = true
  getStudentContactDetail({
    consultId: session.id || '',
    studentUid: session.pyUid
  }).then(u => {
    u.netEaseId = u.studentId
    u.nimAccid = u.studentId
    u.studentJson = u.studentJson ? JSON.parse(u.studentJson) : {}
    u.diagnosisContent = u.diagnosisContent ? JSON.parse(u.diagnosisContent) : ''
    // debugger
    store.commit('imchat/currImTitleInfo', u)
  }).catch(err => {
    Vue.prototype.$message({
      message: err.message,
      type: 'error'
    })
  }).finally(() => {
    getDetail = false
  })
}

let reLoading = false
async function reLoadSession (sessions) {
  // debugger
  // let netEaseIds = []
  const roleId = store.state.roleRelativeData.userInfo.crmRoleId
  // 
  if (reLoading) {
    return
  }
  try {
    reLoading = true
    const params = {
      advisorType: roleId
    }
    if (sessions && sessions.length) {
      params.nimAccIds = [sessions[0].to].join(',')
    }
    const data = await getStudentContactByIdentityId(params)
    store.state.imchat.originSessionList = data
    const userInfos = store.state.imchat.userInfos
    let sessionsArr = Object.values(data)
    sessionsArr = sessionsArr.map(item => {
      if (item.studentId) {
        item.netEaseId = item.studentId
        item.nimAccid = item.studentId
      }
      if (item.status && (userInfos[item.studentId] && userInfos[item.studentId].status)) {
        if ((item.status !== userInfos[item.studentId].status) && (item.status === 1) && roleId === 8) {
          Vue.prototype.$Notice.close('newStuNotice')
          Vue.prototype.$Notice.warning({
            title: '',
            duration: 5,
            name: 'newStuNotice'
          })
        }
      }
      return item
    })
    // 
    store.commit('imchat/updateUserInfo', sessionsArr)
    //  curreSession
    if (store.state.imchat.currSessionId) {
      // debugger
      const userInfos = store.state.imchat.userInfos
      const currentStuId = store.state.imchat.currSessionId.replace('p2p-', '')
      store.dispatch('imchat/setStudentContactDetail', userInfos[currentStuId])
    }
  } catch (err) {
    // debugger
    Vue.prototype.$Message.error({
      content: '' + err.message
    })
  } finally {
    reLoading = false
  }
}

async function gainStuInfo (sessions) {
  // 
  const roleId = store.state.roleRelativeData.userInfo.crmRoleId
  if (!Array.isArray(sessions)) {
    return
  }
  // 
  try {
    const params = {
      advisorType: roleId
    }
    if (sessions) {
      params.nimAccIds = sessions.map(s => s.to).join(',')
    }
    const data = await getStudentContactByIdentityId(params)
    store.state.imchat.originSessionList = data
    let sessionsArr = Object.values(data)
    sessionsArr = sessionsArr.map(item => {
      if (item.studentId) {
        item.netEaseId = item.studentId
        item.nimAccid = item.studentId
      }
      return item
    })
    store.commit('imchat/updateUserInfo', sessionsArr)
    const sessionsId = []
    sessionsArr.forEach(item => {
      if (item.netEaseId) sessionsId.push(item.netEaseId)
    })
    // 
    await store.dispatch('imchat/searchUsers', {
      accounts: sessionsId
    })
    return sessionsArr
  } catch (err) {
    // debugger
    Vue.prototype.$Message.error({
      content: '' + err.message
    })
  }
}

function filterUnbindStu (allSessions, sessionsArr) {
  const userInfos = store.state.imchat.userInfos
  const sessions = []
  if (Array.isArray(allSessions)) {
    allSessions.forEach(item => {
      if (!userInfos[item.to]) {
        const sessionId = `p2p-${item.to}`
        store.dispatch('imchat/deleteSession', sessionId)
      } else {
        sessions.push(item)
      }
    })
  }
  store.commit('imchat/updateSessions', {
    sessions,
    isReplace: false
  })
  sessionsArr.length && mergeSession(sessionsArr)
}

function mergeSession (sessionsArr) {
  const sessionMap = store.state.imchat.sessionMap
  const sessionMergeList = []

  sessionsArr.forEach(item => {
    if (!sessionMap[`p2p-${item.netEaseId}`]) {
      sessionMergeList.push({
        id: `p2p-${item.netEaseId}`,
        to: item.netEaseId,
        scene: 'p2p',
        unread: 0,
        updateTime: item.recentChatTime
      })
    }
  })

  if (sessionMergeList.length) {
    // getMsg(sessionMergeList)
    store.commit('imchat/updateSessions', {
      sessions: sessionMergeList,
      isReplace: false
    })
  }
}

// userIdalias
// userInfo.alias
function syncStudentName (users) {
  if (!Array.isArray(users)) {
    users = [users]
  }
  const me = store.state.roleRelativeData.roleInfo
  const now = new Date().getTime()
  const syncTime =
    localStorage(`time-${me.nimAccid}`) ||
    localStorage.set(`time-${me.nimAccid}`, now)
  const expireTime = 2 * 60 * 60 * 1000
  let userInfos
  if (now - syncTime >= expireTime) {
    localStorage.set(`time-${me.nimAccid}`, now)
    localStorage.remove(me.nimAccid)
    userInfos = {}
  } else if (localStorage(me.nimAccid)) {
    userInfos = localStorage(me.nimAccid) || {}
  }
  userInfos = userInfos || {}
  const accountsNeedSync = []
  users.forEach(user => {
    userInfos[user.account] ||
    user.account === me.nimAccid ||
    accountsNeedSync.push(user.account)
  })
  if (accountsNeedSync.length > 0) {
  } else {
    store.commit('imchat/updateUserInfo', Object.values(userInfos))
  }
}
