function deleteSession ({ state, commit }, sessionId) {
  const nim = NIM
  sessionId = sessionId || ''
  let scene = null
  let account = null
  if (/^p2p-/.test(sessionId)) {
    scene = 'p2p'
    account = sessionId.replace(/^p2p-/, '')
  }
  if (document.visibilityState === 'hidden') {
    return
  }
  // let sessionMap = state.sessionMap
  // if (typeof sessionMap[sessionId] === 'undefined') {
  //   return
  // }
  if (account && scene) {
    nim.deleteSession({
      scene,
      to: account,
      done: function deleteServerSessionDone (error, obj) {
        if (error) {
          return
        }
        nim.deleteLocalSession({
          id: sessionId,
          done: function deleteLocalSessionDone (error, obj) {
            if (error) {
              return
            }
            commit('deleteSessions', [sessionId])
          }
        })
      }
    })
  }
}

function setCurrSession ({ state, commit, dispatch }, sessionId) {
  const nim = NIM
  if (sessionId) {
    const account = util.parseSession(sessionId)
    if (!account.to) {
      Vue.prototype.$message.error('ID...')
      return
    }
    dispatch('searchUsers', {
      accounts: account.to,
      done: syncStudentName
    })
    commit('updateCurrSessionId', {
      type: 'init',
      sessionId
    })
    // console.log('sessionlist+++++++++++', state.sessionlist)
    if (nim) {
      // nimonSessions
      nim.setCurrSession(sessionId)
      commit('updateCurrSessionMsgs', {
        type: 'init',
        sessionId
      })
      // 
      dispatch('sendMsgReceipt')
      dispatch('destroyViewer') // 
    }
  }
}

