/* sysMsgs */
function onSysMsgs (sysMsgs) {
  sysMsgs.forEach(sysMsg => {
    if (sysMsg.type === 'deleteMsg') {
      sysMsg.sessionId = `${sysMsg.scene}-${sysMsg.from}`
      onRevocateMsg(null, sysMsg)
    }
  })
}

function onSysMsg (sysMsg) {
  if (sysMsg.type === 'deleteMsg') {
    sysMsg.sessionId = `${sysMsg.scene}-${sysMsg.from}`
    onRevocateMsg(null, sysMsg)
  }
}

