async function setCaller ({ state, commit, dispatch, rootState }, account) {
  const includeCaller = Object.keys(state.userInfos).includes(account)
  if (!includeCaller) {
    await onSessions([ { id: `p2p-${account}`, to: account } ])
  }
  setTimeout(() => {
    getStudentContactDetail({
      consultId: state.userInfos[account].id
    }).then(res => {
      let userInfo = res
      userInfo.studentJson = res.studentJson ? JSON.parse(res.studentJson) : {}
      userInfo.diagnosisContent = res.diagnosisContent ? JSON.parse(res.diagnosisContent) : ''
      userInfo = Object.assign({}, state.userInfos[account], userInfo)
      commit('crtCallUser', userInfo)
      commit('setBeCalling', true)
      webrtc.playRing('E', 45)
      /**
       * hangup
       * 
       */
      webrtc.beCallTimer = setTimeout(function () {
        if (!webrtc.beCallTimer) return
        console.log('')
        webrtc.beCallTimer = null
        webrtc.reject()
      }, 60 * 1000)
    }).catch(err => {
      vue.$message.error(err.message || err)
    })
  }, 1000)
}

function showMissedCall ({ state, commit, dispatch }, account) {
  const includeCaller = Object.keys(state.userInfos).includes(account)
  if (includeCaller) {
    getStudentContactDetail({
      consultId: state.userInfos[account].id
    }).then(res => {
      const userInfo = res
      const notify = vue.$notify.warning({
        title: '',
        dangerouslyUseHTMLString: true,
        message: `<a>${userInfo.stuRealName}</a>`,
        duration: 0,
        onClick: () => {
          commit('setShowMini', false)
          dispatch('acceptCallToSession', account)
          notify.close()
        }
      })
    }).catch(err => {
      vue.$message.error(err.message)
    })
  } else {
    vue.$message.error('')
  }
}

function acceptCallToSession ({ state, commit, dispatch, rootState }, account) {
  dispatch('setCurrSession', 'p2p-' + account)
  dispatch('getStuPhoneAndNum', account)
  if (rootState.roleRelativeData.userInfo.crmRoleId) {
    // if (rootState.roleRelativeData.userInfo.crmRoleId === 8) { TODO 0424
    dispatch('setStudentContactDetail', state.userInfos[account])
  } else {
    commit('currImTitleInfo', state.userInfos[account])
  }
}

