  playRing (name, count, done) {
    done = done || function () { }
    this.playRingInstance = this.playRingInstance || {}
    const nameMap = {
      A: connecting,
      B: noResponse,
      C: peerBusy,
      D: peerReject,
      E: ring
    }
    const url = nameMap[name]
    console.log('', url)
    const doPlay = (url, playDone) => {
      let audio = document.createElement('audio')
      audio.autoplay = true
      const onEnded = () => {
        if (this.playRingInstance) {
          this.playRingInstance.cancel = null
        }
        audio = null
        playDone()
      }
      audio.addEventListener('ended', onEnded)
      audio.src = url
      this.playRingInstance.cancel = function () {
        audio.removeEventListener('ended', onEnded)
        audio.pause()
        audio = null
      }
    }
    let wrap = () => {
      this.playRingInstance = null
      done()
    }
    for (let i = 0; i < count; i++) {
      wrap = (function (wrap) {
        return function () {
          doPlay(url, wrap)
        }
      })(wrap)
    }
    wrap()
  },
