<template>
  <div class="call-wrapper" :class="studentInfo.diagnosisContent ? 'h270': 'h200'">
    <div class="header" id="call-wrapper-handle">
      
      <Tag v-if="userInfos[studentInfo.nimAccid].consultLabel === 2" color="magenta" style="margin-left: 10px"></Tag>
      <Tag v-if="userInfos[studentInfo.nimAccid].consultLabel === 5" color="magenta" style="margin-left: 10px"></Tag></div>
    <div class="content">
      <p class='fontBold'>
        <span>
          {{ studentInfo.stuRealName || studentInfo.nick }}
        </span>
        <span>| {{ studentInfo.readStatusName || (studentInfo.studentJson && studentInfo.studentJson.readStatusName) || ''  }} | {{ studentInfo.stuGradeName }} | {{ studentInfo.stuLoginName }} </span>
        <span>{{studentInfo.stuSex ? '' : ''}}</span>
      </p>
      <p>{{(studentInfo.studentJson && studentInfo.studentJson.cityName) || ''}}</p>
      <p class='fontBold'>{{studentInfo.studentJson&&studentInfo.studentJson.belongCityName || ''}}</p>
      <p class='fontBold'>{{studentInfo.studentJson&&studentInfo.studentJson.serviceCenterName || ''}}</p>
      <p class='fontBold'>{{studentInfo.studentJson&&studentInfo.studentJson.consultant || ''}}</p>
      <p class='fontBold' v-if='studentInfo.title'>{{studentInfo.title}}</p>
      <template v-if='studentInfo.diagnosisContent'>
        <span class='fontBold'><a v-if='(studentInfo.diagnosisContent && studentInfo.diagnosisContent.diagnosisUrl)' :href='(studentInfo.diagnosisContent && studentInfo.diagnosisContent.diagnosisUrl)' target='_blank'>{{studentInfo.studentJson.cityName }}{{ studentInfo.diagnosisContent&&studentInfo.diagnosisContent.gradeName}}</a></span>
        <span>{{studentInfo.diagnosisContent&&studentInfo.diagnosisContent.gradeName}}</span>
        <span>{{(studentInfo.diagnosisContent&&studentInfo.diagnosisContent.subjectName)}}</span>
        <span>{{(studentInfo.diagnosisContent&&studentInfo.diagnosisContent.score)}}</span>
        <span>{{(studentInfo.diagnosisContent&&studentInfo.diagnosisContent.levelDegreeName)}}</span>
        <span>{{(studentInfo.diagnosisContent&&studentInfo.diagnosisContent.levelName)}}</span>
        <span>{{(studentInfo.diagnosisContent&&studentInfo.diagnosisContent.sourceTypeName)}}</span>
        <span></span>
        <span>{{(studentInfo.diagnosisContent&&studentInfo.diagnosisContent.answerTime)|dateFilter}}</span>
      </template>
    </div>
    <div class="footer">
      <Button type="error" @click="reject"></Button>
      <Button type="success" @click="accept"></Button>
    </div>
  </div>
</template>

<script>
import { dateFormat } from '@/utils/utils'
import util from '@/common/util'
export default {
  name: 'BeCall',
  filters: {
    dateFilter (val) {
      return dateFormat(val)
    }
  },
  props: {
    account: String
  },
  data () {
    return {
    }
  },
  computed: {
    studentInfo () {
      return this.$store.state.imchat.crtCallUser
    },
    userInfos () {
      return this.$store.state.imchat.userInfos
    }
  },
  methods: {
    accept () {
      const browserName = util.browser()
      if (browserName.includes('Chrome')) {
        this.$store.dispatch('imchat/accept')
      } else {
        this.$Message.warning({
          content: '',
          duration: 5
        })
      }
    },
    reject () {
      this.$store.dispatch('imchat/reject')
    }
  }
}
</script>

<style scoped lang="less">
  .call-wrapper {
    background: #eee;
    padding: 10px;
    border-radius: 4px;
    border: 1px solid #000000;
    width: 400px;
    z-index: 10000;
    .header {
      cursor: move;
      font-size: 16px;
      font-weight: bold;
      padding-bottom: 10px;
      margin-bottom: 10px;
      border-bottom: 1px solid #ccc
    }
    .footer {
      text-align: right;
    }
  }
  .h270 {
    height: 270px;
  }
  .h200 {
    height: 200px;
  }
</style>
