// import axios from '@/libs/api.request'
import { getLastIdentity, myIdentity } from '@/api/crm'
import { getEmpDeptRolesByEmpNo, getUserResourceUrlList } from '@/api/auth'
import Vue from 'vue'
const state = () => ({
  roleList: [],
  userInfo: {},
  roleInfo: {},
  allowButtonList: [], //  
  roleIds: [ 2, 3, 4, 5 ] //  
})
const mutations = {
  setRoleList (state, data) {
    state.roleList = data
  },
  setUserInfo (state, data) {
    state.userInfo = data
  },
  setRoleInfo (state, data) {
    state.roleInfo = data
  },
  setAllowButtonList (state, allowButtonList) {
    state.allowButtonList = allowButtonList
  }
}

const actions = {
  async gainRoleList (ctx) {
    // const { data } = { 'success': true, 'code': 0, 'msg': '', 'data': { 'currentTime': '2019-08-26 16:51:33', 'activeIdentityId': 111780, 'list': [{ 'identityId': 111780, 'userId': 2496, 'userName': '', 'userWorkCode': '076757', 'departId': 252, 'departFullName': '------', 'departIdList': [1, 2, 10, 14, 60, 252], 'departName': '', 'crmRoleId': 5, 'roleName': '', 'createTime': 1566809474, 'createTimeMsg': '2019-08-26 16:51:14', 'status': 1, 'statusDisplay': '', 'remark': '', 'rank': 1, 'editable': true, 'deleteable': false }, { 'identityId': 111772, 'userId': 2496, 'userName': '', 'userWorkCode': '076757', 'departId': 1, 'departFullName': '', 'departIdList': [1], 'departName': '', 'crmRoleId': 1, 'roleName': '', 'createTime': 1563892022, 'createTimeMsg': '2019-07-23 22:27:02', 'status': 1, 'statusDisplay': '', 'remark': '', 'rank': 1, 'editable': true, 'deleteable': false }] } }
    // ctx.commit('setRoleList', data)
    let req = {
      // empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
    }
    try {
      /**
       * 
       */
      const roleList = await getEmpDeptRolesByEmpNo(req)
      ctx.commit('setRoleList', roleList)

      /**
       * 
       */
      let userInfo = await getLastIdentity(req)
      // getLastIdentity
      // if (userInfo && userInfo.authIdentityId) {
      //   const list = roleList.filter(obj => obj.identityId === userInfo.authIdentityId)
      //   if (list.length === 0) {
      //     userInfo = roleList[0]
      //   } else {
      //     userInfo = list[0]
      //   }
      // }
      ctx.commit('setUserInfo', userInfo)
      if (userInfo.authIdentityId) {
        localStorage.setItem('authIdentityId', userInfo.authIdentityId)
      }
      const allowButtonList = await getUserResourceUrlList({
        type: 2,
        identityId: userInfo.authIdentityId
      })

      if (allowButtonList) {
        ctx.commit('setAllowButtonList', allowButtonList || [])
      }

      const resolveData = {
        roleList,
        latestRole: userInfo
      }

      return resolveData
    } catch (err) {
      Vue.prototype.$Message.error({
        content: '' + err.message
      })
    }
  },
  async gainRoleInfo (ctx) {
    try {
      /**
       * im
       */
      const roleInfo = await myIdentity()
      ctx.commit('setRoleInfo', roleInfo)
    } catch (err) {
      Vue.prototype.$Message.error({
        content: '' + err.message
      })
    }
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
