// 
// 

import apiTrackData from '@/config/tal-sensors-config'
import { runEnv } from './util'

// 
export const apiTrack = (instance) => {
  instance.interceptors.request.use(
    config => {
      // url
      const path = config.url[0] !== '/' ? '/' + config.url : config.url
      let baseURL = config.baseURL
      if (baseURL[baseURL.length - 1] === '/') baseURL = baseURL.slice(0, -1)
      baseURL = baseURL.split('/')[baseURL.split('/').length - 1]
      const url = baseURL + path
      if (apiTrackData.api[url]) {
        const { trackId, props } = apiTrackData.api[url]
        let params = {}
        const method = config.method
        let request = {}
        if (method.toLowerCase() === 'get' && config.params) {
          request = config.params
        } else if (method.toLowerCase() === 'post' && config.data) {
          request = config.data
        }
        // 
        if (props === 'all') {
          params = { query_json: request }
        } else {
          props.forEach(item => {
            params[item] = request[item]
          })
        }
        if (XesAnalytics) {
          XesAnalytics.track(trackId, params)
        }
      }
      return config
    }
  )
}

// 
export const viewTrack = (router, Vue) => {
  // 
  router.afterEach((to, from) => {
    if (!XesAnalytics.view) return
    // 
    const routerName = to.name
    const track = apiTrackData.view[routerName] || {}

    if (track && XesAnalytics && track.enterTrackId) {
      XesAnalytics.view(track.enterTrackId)
    }
    // 
    const fromRouterName = from.name

    const track2 = apiTrackData.view[fromRouterName] || {}
    if (track2 && XesAnalytics && track2.leaveTrackId) {
      XesAnalytics.view(track2.leaveTrackId)
    }
  })

  const triggerView = () => {
    if (!XesAnalytics.view) return
    const pathNames = location.pathname.split('/')
    const routerName = pathNames[pathNames.length - 1]
    const track = apiTrackData.view[routerName] || {}
    if (document.visibilityState === 'visible' && track) {
      XesAnalytics.view(track.enterTrackId)
    }

    if (document.visibilityState === 'hidden' && track.leaveTrackId) {
      XesAnalytics.view(track.leaveTrackId)
    }
  }

  // 
  document.addEventListener('visibilitychange', triggerView)
}

// 
export const initTrack = (Vue) => {
  if (XesAnalytics) {
    const XesLog = new XesAnalytics({
      source: '8', // 3=apph55=apph58=Web9=ipad10=pad 100=
      event_env: runEnv() === 'prod' ? 'prod' : 'test', // test= prod=
      proj_id: 'fe-service-platform', // 
      auto_track: true, // 
      show_log: true, // 
      collect_tags: {} // 
    })
    const roleInfo = Vue.$store.state.roleRelativeData.userInfo
    const userInfo = Vue.$store.getters.userInfo
    XesLog.setProfile({
      age: '',
      app_id: '',
      bind_phone: '',
      city: '',
      city_code: roleInfo.cityCode, // 
      city_id: '',
      gid: '',
      grade: '',
      grade_id: '',
      open_id: '',
      province: '',
      sex: '',
      stu_id: '',
      uid: '',
      union_id: '',
      user_id: userInfo.empNo, // 
      user_name: userInfo.name, // 
      user_role: roleInfo.crmRoleId // id
    })
    // 
    window.XesAnalytics = XesLog
  }
}

export const XesAnalyticsTrack = (id, params) => {
  if (XesAnalytics) {
    XesAnalytics.track(id, params)
  }
}
