<template>
  <div class="chat-title">
    <span v-if="!!currImTitleInfo && !!currImTitleInfo.netEaseId">
      <p class='fontBold'>
        <span class="name" @click="goCustomerDetail">
          {{ currImTitleInfo.stuRealName || currImTitleInfo.nick }}
        </span>
        <span>| {{ currImTitleInfo.readStatusName || (studentJson.readStatusName) || ''  }} | {{ currImTitleInfo.stuGradeName || studentJson.stuGradeName }} | {{ currImTitleInfo.stuLoginName || studentJson.stuLoginName }} </span>
        <template >
          <span>{{(studentJson.stuSex === 1) ? '' : ''}}</span> <span>{{(studentJson.cityName) || ''}}</span>
        </template>
      </p>
      <template>
        <span class='fontBold'>{{studentJson.belongCityName || ''}}</span>
        <span class='fontBold'>{{studentJson.serviceCenterName || ''}}</span>
        <span class='fontBold'>{{studentJson.consultant || ''}}</span>
        <span class='fontBold' v-if='currImTitleInfo.title'>{{currImTitleInfo.title || ''}}</span>
        <template v-if='diagnosisContent.diagnosisUrl'>
          <span class='fontBold'><a v-if='(diagnosisContent.diagnosisUrl)' :href='(diagnosisContent.diagnosisUrl)' target='_blank'>{{studentJson.cityName }}{{ diagnosisContent.gradeName}}</a></span>
          <span>{{diagnosisContent.gradeName}}</span>
          <span>{{(diagnosisContent.subjectName)}}</span>
          <span>{{(diagnosisContent.score)}}</span>
          <span>{{(diagnosisContent.levelDegreeName)}}</span>
          <span>{{(diagnosisContent.levelName)}}</span>
          <span>{{(diagnosisContent.sourceTypeName)}}</span>
          <!--<span>{{(diagnosisContent.isValid) === 1 ? '' : ''}}</span>-->
          <span></span>
          <span>{{(diagnosisContent.answerTime)|dateFilter}}</span>
        </template>
      </template>
    </span>
    <span v-else></span>
  </div>
</template>

<script>
import { dateFormat } from '@/utils/utils'
export default {
  filters: {
    dateFilter (val) {
      return dateFormat(val)
    }
  },
  data () {
    return {
    }
  },
  computed: {
    currImTitleInfo () {
      return this.$store.state.imchat.currImTitleInfo
    },
    studentJson () {
      return this.currImTitleInfo.studentJson || {}
    },
    diagnosisContent () {
      return this.currImTitleInfo.diagnosisContent || {}
    },
    crmRoleId () {
      return this.$store.state.roleRelativeData.userInfo.crmRoleId
    }
  },
  methods: {
    goCustomerDetail () {
      this.$emit('goCustomerDetail', this.currImTitleInfo)
    },
    diagnosisHandle () {
      this.$emit('diagnosisHandle', this.currImTitleInfo)
    }
  }
}
</script>

